.background_titel_article{
    position: absolute;
    width: 100%;
    height: 100%; /* apply the blur */
    pointer-events: none; /* make the overlay click-through */
    background-image: url("../media/backgroundblur.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}
