  .title_article{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1em;
  }
  
  .title_pp{
    position: relative;
    height: 8rem;
    width: 8rem;
    border-radius: 50%;
    overflow: hidden;
    border: 0.25rem white solid;
    background-color: white;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .title_pp > img{
    position: relative;
    width: 100%;
    height: 100%;
  }

  .title_frame{
    position: relative;
    height: 10rem;
    width: 30rem;
    color: white;
  }
  
  .title_text{
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    border: 0.5rem white solid;
    bottom: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
    font-weight: bold;
    text-align: center;
  }
  
  .title_underscore{
    position: absolute;
    height: 2rem;
    left: 0px;
    right: 0px;
    bottom: 0em;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    text-align: center;
  }