.ViewArticle{
  min-height: 100vh;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  flex-grow: 1;
  background-color: rgb(255, 255, 255);
}

.ViewArticle > div{
  width: 100%;
}

.ViewArticleTitle{
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4rem;
  color: white;
  font-weight: bold;
  -webkit-text-stroke: 2px black;
}

.ViewArticleContent{
  min-height: 40vh;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.ViewFirst{
  background-color: rgb(44, 255, 255);
  position: absolute;
  transform: translate(0, -50%);
  height: 80%;
  top: 50%;
  left: 0%;
  width: max(min(35rem, calc(100% - 32vh)), 200px);
}

.ViewFirstTriangle {
  background-color: rgb(44, 255, 255);
  position: absolute;
  transform: translate(0, -50%);
  height: 80%;
  top: 50%;
  left: max(min(35rem, calc(100% - 32vh)), 200px);
  aspect-ratio: 1;
  -webkit-clip-path: polygon(0 0, 70% 0, 0 100%, 0% 100%);
  clip-path: polygon(0 0, 70% 0, 0 100%, 0% 100%);
}

.ViewSecond{
  background-color: rgb(44, 255, 255);
  position: absolute;
  transform: translate(0, -50%);
  height: 80%;
  top: 50%;
  right: 0%;
  width: max(min(35rem, calc(100% - 32vh)), 200px);
}

.ViewSecondTriangle {
  background-color: rgb(44, 255, 255);
  position: absolute;
  transform: translate(0, -50%);
  height: 80%;
  top: 50%;
  right: max(min(35rem, calc(100% - 32vh)), 200px);
  aspect-ratio: 1;
  -webkit-clip-path: polygon(30% 0, 100% 100%, 100% 0);
  clip-path: polygon(30% 0, 100% 100%, 100% 0);
}